






















import { Component, Vue } from "vue-property-decorator";
import { Prop } from "vue-property-decorator";

// 定义productions的类型
interface Productions {
  src?: string;
  href?: string;
  title: string;
  content: string;
}

@Component
export default class imgDiv extends Vue {
  @Prop() productions!: Productions;
  @Prop() img!: string;

  goHref(e, href) {
    e.preventDefault();
    if (href) {
      window.open(href);
      return;
    }
    this.$router.push("/stereograph");
  }
}
